.farm {
    padding: 80px 0;

    @media screen and (max-width: 768px) {
        padding: 30px 0;
    }

    .total {
        display: flex;
        padding: 0 0 64px;

        @media screen and (max-width: 768px) {
            display: block;
            padding: 0 0 40px;
        }

        .item {
            background: #181818;
            border: 1px solid rgba(0, 0, 0, 0.08);
            box-shadow: 0px 8px 32px rgba(138, 11, 11, 0.05);
            border-radius: 16px;
            padding: 24px;
            display: flex;
            align-items: center;
            flex: 0 0 calc(33.333333% - 16px);

            @media screen and (max-width: 768px) {
                margin: 0 16px 20px !important;
            }

            &:nth-child(2) {
                margin: 0 24px;
            }

            .icon {
                background: rgba(255, 255, 255, 0.06);
                border-radius: 10px;
                height: 64px;
                width: 64px;
                display: flex;
                align-items: center;
                justify-content: center;
                margin: 0 10px 0 0;

                .svg {
                    height: 32px;
                    width: 32px;
                }
            }

            .box {
                flex: 1;

                .txt {
                    font-family: 'Poppins';
                    font-style: normal;
                    font-weight: 400;
                    font-size: 16px;
                    line-height: 24px;
                    color: rgba(#fff, .6);
                }

                .num {
                    font-family: 'Kanit';
                    font-style: normal;
                    font-weight: 500;
                    font-size: 24px;
                    line-height: 36px;
                    margin: 4px 0 0;
                    display: flex;
                    align-items: center;
                    justify-content: space-between;

                    .btn {
                        background: rgba(113, 255, 58, 0.1);
                        border-radius: 8px;
                        width: 80px;
                        height: 32px;
                        font-family: 'Poppins';
                        font-style: normal;
                        font-weight: 400;
                        font-size: 18px;
                        transition: all 0.3s;

                        &:hover {
                            opacity: 0.7;
                        }
                    }
                }
            }
        }
    }

    .filter {
        font-family: 'Poppins';
        font-style: normal;
        font-weight: 500;
        font-size: 16px;
        line-height: 22px;
        margin: 0 0 24px;
        padding: 0 16px;
    }

    .list {
        display: flex;

        @media screen and (max-width: 768px) {
            display: block;
        }

        .item {
            flex: 0 0 calc(33.333333% - 16px);
            background: #181818;
            border: 1px solid rgba(0, 0, 0, 0.08);
            border-radius: 20px;
            padding: 30px 24px;

            @media screen and (max-width: 768px) {
                margin: 0 16px 20px !important;
            }

            &:nth-child(2) {
                margin: 0 24px;
            }

            .header {
                display: flex;
                align-items: center;
                justify-content: space-between;
                border-bottom: 1px solid rgba(#fff, .1);
                padding: 0 0 20px;
                margin: 0 0 6px;

                .name {
                    .symbol {
                        font-family: 'Poppins';
                        font-style: normal;
                        font-weight: 600;
                        font-size: 28px;
                        line-height: 42px;
                        color: #FFFFFF;
                    }

                    .num {
                        font-family: 'Poppins';
                        font-style: normal;
                        font-weight: 500;
                        font-size: 14px;
                        line-height: 22px;
                        text-align: center;
                        color: #FFB422;
                        border: 2px solid #FFB422;
                        border-radius: 13px;
                        padding: 0 13px;
                        margin: 4px 0 0;
                    }
                }

                .tokens {
                    width: 64px;
                    height: 64px;
                    position: relative;

                    .tokenA {
                        position: absolute;
                        border-radius: 100%;
                    }

                    .tokenB {
                        position: absolute;
                        bottom: 0;
                        right: 0;
                        z-index: 1;
                        border-radius: 100%;
                        border: 1px solid #000;
                    }
                }
            }

            .main {
                .line {
                    display: flex;
                    justify-content: space-between;
                    padding: 24px 0 0;

                    .txt {
                        font-family: 'Poppins';
                        font-style: normal;
                        font-weight: 500;
                        font-size: 16px;
                        line-height: 20px;
                        color: rgba(255, 255, 255, 0.6);
                    }

                    .num {
                        text-align: right;
                        font-family: 'Poppins';

                        span {
                            font-style: normal;
                            font-weight: 400;
                            font-size: 18px;
                            line-height: 20px;
                            color: #FFFFFF;
                            display: block;
                        }

                        em {
                            font-style: normal;
                            font-weight: 400;
                            font-size: 14px;
                            line-height: 20px;
                            color: rgba(#fff, .6);
                        }

                        strong {
                            background: rgba(113, 255, 58, 0.1);
                            border-radius: 8px;
                            padding: 6px 16px;
                            font-style: normal;
                            font-weight: 400;
                            font-size: 18px;
                            line-height: 20px;
                            color: #71FF3A;
                        }
                    }
                }
            }

            .btn {
                display: flex;
                padding: 40px 0 20px;

                .redeem {
                    border: 1px solid #71FF3A;
                    border-radius: 12px;
                    height: 56px;
                    margin: 0 16px 0 0;
                    min-width: 42px;
                    padding: 0;
                    font-family: 'Poppins';
                    font-style: normal;
                    font-weight: 600;
                }

                .stake {
                    background: linear-gradient(243.59deg, #71FF3A 14.24%, #00D3BA 83.41%);
                    border-radius: 12px;
                    height: 56px;
                    font-family: 'Poppins';
                    font-style: normal;
                    font-weight: 600;
                    font-size: 16px;
                    line-height: 16px;
                    color: #000;
                    flex: 1;
                    transition: all 0.3s;

                    &:hover {
                        opacity: 0.7;
                    }
                }
            }

            .link {
                display: flex;
                justify-content: center;
                align-items: center;
                cursor: pointer;
                transition: all 0.3s;

                &:hover {
                    opacity: 0.7;
                }

                span {
                    font-family: 'Poppins';
                    font-style: normal;
                    font-weight: 500;
                    font-size: 16px;
                    line-height: 20px;
                    color: #71FF3A;
                    margin: 0 10px 0 0;
                }

                svg {
                    fill: #71FF3A;
                }
            }
        }

    }
}