.futuristic-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: row !important;
  padding: 0 40px;
  height: 70px;

  @media screen and (max-width: 768px) {
    height: 44px;
    padding: 0 16px;
  }

  .logo {
    width: 128px;
    height: 36px;

    @media screen and (max-width: 768px) {
      width: 110px;
      height: 34px;
    }
  }
}