.loading {
    display: inline-block;

    @keyframes turn {
        0% {
            transform: rotate(0deg)
        }

        25% {
            transform: rotate(90deg)
        }

        50% {
            transform: rotate(180deg)
        }

        75% {
            transform: rotate(270deg)
        }

        100% {
            transform: rotate(360deg)
        }
    }

    .icon {
        animation: turn 1s linear infinite;
        border-radius: 100%;
        width: 100%;
        height: 100%;
    }
}