.activity {
    height: 38px;

    &:global(.MuiButton-text) {
        font-size: 16px;
        font-weight: 500;
        border: 1px solid #fff;
        color: #fff;
        border-radius: 8px;
        padding: 0 16px;

        &:hover{
            border: 1px solid #71FF3A;
            color: #71FF3A;
        }
    }

    

    @media screen and (max-width: 768px){
        font-size: 14px;
        padding: 0 12px;
        height: 32px;
    }

    .logo {
        width: 24px;
        height: 24px;
        margin: 0 0 0 10px;
        @media screen and (max-width: 768px){
            display: none;
        }
    }
}

.connect{
    background: linear-gradient(243.59deg, #71FF3A 14.24%, #00D3BA 83.41%);
    border-radius: 8px !important;
    height: 38px;
    font-family: 'Poppins';
    font-style: normal;
    font-weight: 500;
    font-size: 16px;

    @media screen and (max-width: 768px) {
        font-size: 14px;
        height: 32px;
    }
}