.modal {
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all 0.3s;

    .box {
        background: #181818;
        border: 1px solid rgba(0, 0, 0, 0.08);
        border-radius: 20px;
        padding: 20px;
        width: 450px;

        @media screen and (max-width: 768px) {
            max-width: 390px;
            margin: 0 10px;
        }

        .title {
            display: flex;
            justify-content: space-between;
            padding: 0 0 20px;
            align-items: center;

            .txt {
                font-family: 'Poppins';
                font-style: normal;
                font-weight: 500;
                font-size: 20px;
                line-height: 30px;
                color: #FFFFFF;
            }

            button {
                margin: -10px -10px 0 0;

                .icon {
                    width: 24px;
                    height: 24px;
                }
            }
        }

        .account {
            border: 1px solid #373737;
            border-radius: 12px;
            margin: 0 0 24px;
            display: flex;
            align-items: center;
            justify-content: space-between;
            padding: 16px 20px;

            @media screen and (max-width: 768px) {
                padding: 10px;
            }

            .left {
                display: flex;
                align-items: center;

                .logo {
                    width: 24px;
                    height: 24px;

                    @media screen and (max-width: 768px) {
                        width: 16px;
                        height: 16px;
                    }
                }

                .txt {
                    font-family: 'Poppins';
                    font-style: normal;
                    font-weight: 500;
                    font-size: 20px;
                    line-height: 30px;
                    color: #FFFFFF;
                    margin: 0 5px 0 10px;

                    @media screen and (max-width: 768px) {
                        font-size: 16px;
                        line-height: 20px;
                    }
                }

                .icon {
                    width: 20px;
                    height: 20px;
                    fill: rgba(#fff, .6);

                    @media screen and (max-width: 768px) {
                        width: 12px;
                        height: 12px;
                    }
                }
            }

            .btn {
                height: 32px;
                padding: 0 8px;
                font-family: 'Poppins';
                font-style: normal;
                font-weight: 400;
                font-size: 14px;
                text-align: center;
                color: #FFFFFF;
                border: 1px solid #FFFFFF;
                border-radius: 8px;

                @media screen and (max-width: 768px) {
                    font-size: 12px;
                }
            }
        }

        .tab {
            .tabs {
                display: flex;
                justify-content: space-between;

                .txt {
                    display: flex;

                    .item {
                        color: rgba(#fff, .3);
                        font-family: 'Poppins';
                        font-style: normal;
                        font-weight: 500;
                        font-size: 20px;
                        line-height: 40px;
                        height: 40px;
                        padding: 0 8px;

                        &.cur,
                        &:hover {
                            color: #fff;
                        }
                    }
                }
            }

            .tabPanel {
                border: 1px solid #373737;
                border-radius: 12px;
                padding: 20px;
                margin: 20px 0 0;

                .balance {
                    .item {
                        display: flex;
                        align-items: center;
                        justify-content: space-between;
                        margin: 0 0 20px;

                        &:last-child {
                            margin: 0;
                        }

                        .info {
                            display: flex;
                            align-items: center;

                            .token {
                                display: flex;
                                align-items: center;

                                .symbol {
                                    font-family: 'Poppins';
                                    font-style: normal;
                                    font-weight: 600;
                                    font-size: 14px;
                                    margin: 0 10px;
                                }
                            }

                            .num {
                                font-family: 'Poppins';
                                font-style: normal;
                                font-weight: 600;
                                font-size: 14px;
                            }
                        }

                        .btn {
                            background: rgba(113, 255, 58, 0.1);
                            border-radius: 8px;
                            padding: 0 8px;
                            height: 32px;
                            transition: all 0.3s;
                            font-size: 14px;

                            &:hover {
                                opacity: 0.7;
                            }
                        }

                    }
                }

                .transaction {
                    .item {
                        display: flex;
                        margin: 0 0 20px;

                        &:last-child {
                            margin: 0;
                        }

                        .type{
                            margin: 5px 0 0;
                        }

                        .info {
                            flex: 1;
                            margin: 0 0 0 10px;

                            .msg {
                                .txt {
                                    font-family: 'Poppins';
                                    font-style: normal;
                                    font-weight: 500;
                                    font-size: 16px;
                                    line-height: 24px;
                                }

                                .link {
                                    fill: rgba(#fff, .6);
                                    margin: 0 0 0 10px;
                                }
                            }

                            .time {
                                font-family: 'Poppins';
                                font-style: normal;
                                font-weight: 400;
                                font-size: 14px;
                                line-height: 28px;
                                color: rgba(#fff, .6);
                            }
                        }
                    }
                }
            }
        }
    }
}